/* src/styles/footer.module.css */

.footer {
  background-color: #121212;
  color: var(--text-color);
  text-align: center;
  padding: 0 var(--spacing-md);
  border-top: 2px solid var(--primary-color-dark);
  width: 95vw;
  margin: 0 auto;
}

.footerContent {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: var(--spacing-md);
}

.footer p {
  margin: 0;
  font-size: var(--font-size-md);
}





.socialLinks a:hover {
  color: var(--text-color);
}


.linkButton {
  background: none;
  border: none;
  color: var(--white);
  padding: 0;
  margin: 5px;
  cursor: pointer;
}

.linkButton:hover {
  color: var(--primary-color);
}